*,*::before,*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
	background: #000;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-X: hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
Header {
	position: absolute;
	padding-top: 10px;
	font-size: 1.5rem;
	text-align: center;
	font-family: 'Water Brush', 'Open Sans', Helvetica, sans-szerif;
	text-shadow:
	  -1px 2px 1.5px #767676, 
      -2px 4px 1.5px #787878, 
      -3px 6px 1.5px #7a7a7a,
      -4px 8px 1.5px #7f7f7f;
	color: #1CDBF5;
	width: 100vw;
	height: 8vh;
	background: #131A28;
}
.icon {
	position:absolute;
	top: 15px;
	left: max(7vw,25px);
	width: 60px;
	height:35px;
}
.Nav {
	position: absolute;
	top: 5.5vh;
	left: min(-30px,-12vw);
	width: 100vw;
	height:50px;
}
#menu-icon {
	display:none;
}
.menu-icon {
	position:absolute;
	z-index:2;
	width:50px;
	height: 50px;
	right: -2vw;
	cursor: pointer;
}
i {
	position: absolute;
	top: 20px;
	width: 35px;
	height: 5px;
	border-radius: 50px;
	background: #1CDBF5;
}
i::before {
	position:absolute;
	content: '';
	top: -10px;
	width: 50px;
	height: 100%;
	border-radius: 50px;
	transform: rotate(0);
	transform-origin: 11px 9px;
	background: #1CDBF5;
	transition: transform .5s ease ;
}
i::after {
	position:absolute;
	content: '';
	top: 10px;
	width: 50px;
	height: 100%;
	border-radius: 50px;
	transform: rotate(0);
	transform-origin: 15px -2px;
	background: #1CDBF5;
	transition: transform .5s ease ;
}
input:checked ~ .menu-icon > i {
	background: transparent;
}
input:checked ~ .menu-icon > i::before {
	transform:rotate(45deg);
	transition: transform .5s ease ;
}
input:checked ~ .menu-icon > i::after {
	transform:rotate(-45deg);
	transition: transform .5s ease ;

}
.Nav-list {
	display: flex;
	position:absolute;
	z-index: -1;
	top: 70px;
	right: 35px; 
	padding-top: 15px;
	padding-bottom: 10px;
	transform: translateY(-100px);
	flex-direction: column;
	background: #131A28;
	font-family: 'Shadows Into Light', 'Water Brush', 'Open Sans', Helvetica, sans-serif;
	opacity: 0;
	transition: opacity 1s ease ,
	 transform 1s ease ,
	 z-index 1s ease 1s;
}
input:checked~.Nav-list {
	z-index: 1;
	opacity: 1;
	transform: translateY(0);
	transition: opacity .8s ease .2s ,
	 transform .8s ease ;
}
li {
	position:relative;
	font-size: 1.4rem;
	text-align: center;
	padding-top: 10px;
	margin-left: 2px;
	list-style-type: none;
	width: 130px;
	height:100%;
}
.link { 
	color: #131A28;
	text-shadow: 2px 2px 1px #1CDBF5;
	text-decoration: none;
}
Footer {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: .95rem;
	padding: 15px 25px ;
	z-index: 5;
	top: 220vh;
	width: 100vw;
	height: 30vh;
	color: lightgray;
	background: #112654;
}
.content-footer {
	position:absolute;
	padding: 15px;
	width: 100vw;
	height: 30vh;
	display: flex;
	justify-content: space-evenly;
	align-items: flex-start;
	flex-direction: row;
	border: 3vw solid #00cc99;
	border-right-width: 6vw;
	border-top-width: 4.5vh;
	border-bottom-width: 2vh;
	opacity: .7;
}
.logo {
	width: 50px; 
	height: 50px;
}
.first-list-footer,.second-list-footer,.third-list-footer {
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-direction: column;
	padding-bottom: 10px;
}
.second-list-footer {
	justify-content: flex-start;
}
.link-footer {
	padding-top: 4px;
	color: #1CDBF5;
	text-decoration: none;
}
.cop {
	transform: translateX(15px) translateY(115px);
}
span {
	position: relative;
	background: white;
	cursor: pointer;
}



