.first-page {
	display: flex;
	position: absolute;
	justify-content: center;
	left: 10vw;
	width: 80vw;
	height: 100vh;
	background: url('https://as1.ftcdn.net/v2/jpg/04/77/51/66/1000_F_477516617_Obj0rZobQoUT6vtbmEuWAd3VvuF4KkdK.jpg');
	background-size: cover;
