	overflow: hidden;
}
.img {
	display: flex;
	justify-content: center;
	position: absolute;
	top: 10vh;
	width: 80vw;
	height:50vh; 
	filter: contrast(120%);}
.img::before, .img::after {
	position: absolute;
	top: -10vh;
	left: 2vw;
	content: '';
	width: 20vw;
	height: 100vh;
	background: url('https://ak.picdn.net/shutterstock/videos/1013216561/thumb/1.jpg');
	background-size: contain; 
	clip-path: polygon( 30% 10%, 100% 0, 100% 100%, 15% 90% );
}
.img::after {
	left: 58vw;
	transform: rotate(180deg);
}
.click {
	position: absolute;
	z-index: 2;
	top: 80vh;
	font-size: min(3rem, 5vw);
	font-weight: 800;
	color: hsl(120,90%,25%);  
	text-shadow: -2px -2px 0 #bbb, 2px -2px 0 #bbb, -2px 2px 0 #bbb, 2px 2px 0 #bbb;
	font-family: 'Water Brush', 'Open Sans', Helvetica, sans-serif;
	animation: twinkle .8s ease-in-out .5s alternate infinite;
}

.name {
	position: absolute;
	top: 20vh;
	width: 60vw;
	height: 40vh;
	font-size: min(10rem, 14.5vw);
	font-weight: 900;
	text-align: center;
	font-family: 'Creepster', 'Open Sans', Helvetica, sans-serif;  
	text-shadow: -3px -3px 0 #0007, 3px -3px 0 #0007, -3px 3px 0 #0007, 3px 3px 0 #0007;
	color: hsl(61,70%,60%);
}
.side {
	position: absolute;
	bottom:0;
	left: 0px;
	width: 50px;
	height: 45vh;
	filter: blur(8px);
	background:linear-gradient(to right, red , orange, green);
	box-shadow: 
	1060px 0 0 0 red,
	1045px 0 0 0 orange,
	1030px 0 0 0 green
	;
}


@keyframes twinkle {
	0% {
		opacity: 1;
	}
	20% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}
