.score-page, 
.about-page,.no-page {
	position: absolute;
	width:100vw;
	height:100vh;
	background: #131A28;
	font-family: 'Montserrat', sans-serif;
}
.help-page {
	position: absolute;
	width:100vw;
	height:250vh;
	background: #131A28;
	font-family: 'Montserrat', sans-serif;
}
.about-content{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin-top:-8vh;
	padding: 8vw;
	padding-top: 0px;
	position:absolute;
	top: 20vh;
	left: 10vw;
	width: 80vw;
	height: 90%;
	color: white;
	background: #fff2;
}
.help-content {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin-top: 0vh;
	padding-top: calc(1750px - 100vw);
	padding-left: 6vw;
	padding-right: 20vw;
	position:absolute;
	top: 20vh;
	left: 10vw;
	width: 95vw;
	height: 70%;
	color: white;
	overflow: scroll;
	overflow-X: hidden;
	background: #fff2;
}
.side-help {
	position: absolute;
	z-index: 3;
	top:0px;
	right:-14.5vw;
	width:25vw;
	height: 225vh;
	background:#131A28;
}
.heading {
	position: relative;
	top: -2vw;
	border-bottom: 3px solid #131A28;
	text-align: center;
	text-shadow: 4px 4px 1px black;
}
h4,h5 {
	padding-top: 10px;
	padding-bottom: 5px;
	font-size: 1.06rem;
	text-shadow: 3px 3px 1px black;
}
p a {
	color: #1CDBF5;
	text-shadow:4px 3px 2px #660511;
	font-size: 1.05rem;
	font-weight: 600;
	font-style: italic;
} 
.help-content > .heading ~ ul > li {
	position: relative;
	padding-top: 25px;
	font-size: 1.1rem;
	padding-bottom: 30px;
	height: max-content;
	padding-left: 10vw;
	padding-right: 6vw;
	width: 80vw;
}
.pos {
	position: relative;
	top: -115px;
	left: 150px;
	width: 5px;
	height:100px;
	background:#1CDBF5;
	box-shadow: 35px 0 0 0 #1CDBF5;
	float: left;
}
.pos::before {
	position: absolute;
	content: '';
	top: -18px;
	left: 18px;
	width: 5px;
	height: 100px;
	transform: rotate( 90deg );
	background: #1CDBF5;
	box-shadow: 35px 0 0 0 #1CDBF5;
}
.pos::after {
	position: absolute;
	left: -20px;
	content: 'X';
	color: #1CDBF5;
	font-size: 1.5rem;
	font-weight: 900;
	border-radius: 50%;
	font-family: 'Creepster', 'Open Sans', Helvetica, sans-serif; 
}
.second-position::after {
	left: 14px;
}
.third-position::after {
	top: 35px;
	left: 14px;
} 
.lowheading {
	position: relative;
	width: 50px;
	height: 50px;
	padding-bottom: 20vh;
	text-decoration: underline;
}
.profile {
	position: absolute;
	top: 200px;
	left: 100px;
	width: 300px; 
	height: 300px;
	font-size: 2rem;
	color: #00cc99;
	background: white;
	opacity: .5;
}

@media only screen and (max-width: 600px) {
	.help-content {
		padding-top: calc(2060px - 140vw);
	}
    .heading-text {
        color: white;
        font-size: 35px;
        font-weight: bolder;
        text-align: center;
        margin: 30px 10px;
    }

    .square {
        min-width: 80px;
        min-height: 80px;
        font-size: 45px;
        font-weight: bolder;
        padding: 15px;
        display: flex;
        align-items: center;
		background-color: #131A28;
        justify-content: center;
    }

    .b-bottom-right {
        border-right: 10px solid #1CDBF5;
        border-bottom: 10px solid #1CDBF5;
    }

    .b-bottom {
        border-bottom: 10px solid #1CDBF5;
    }

    .b-right {
        border-right: 10px solid #1CDBF5;
    }

    .clear-button {
		position:absolute;
		top: 100px;
		left: 15px;
		width: 35vw;
		height:8vw;
		margin-top: 69vh;
		color: black;
		text-align: center;
		text-decoration: none;
		font-size: min(4vw, 1.5rem);
		font-weight: 600;
		padding-bottom: 5px;
		border-radius: 50px;
		background:#1CDBF5;
		border: 2px solid black;
		cursor: pointer;
    }
}